.orderForm {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
  }
  
  .orderForm__label {
    margin-bottom: 4px;
    font-size: 12px;
    color: var(--color-grey1);
  }
  
  .orderForm-wrapper {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
  }
  
  .orderForm__item {
    width: 100%;
    margin-bottom: 27px;
    padding: 8px 0 8px;
    border: none;
    border-bottom: 1px solid var(--color-grey1);
    font-size: 16px;
    font-weight: 400;
  }
  
  .orderForm__column {
    display: flex;
    flex-direction: column;
  }
  
  .orderForm__item.error {
    font-size: 16px;
    color: var(--color-error);
    border-bottom: 1px solid var(--color-error);
  }

  .error {
    font-size: 12px;
    color: var(--color-error);
  }

  .orderForm textarea:first-of-type {
    margin-bottom: 15px;
    padding: 4px 0;
  }
  
  .orderForm select {
    color: var(--color-grey1);
    background-color: var(--color-white);
  } 
  
  .order-button {
    margin-bottom: 50px;
    height: 60px;
    font-size: 20px;
    font-weight: 700;
    background-color: var(--color-white);
    border: 1px solid var(--color-black);
    cursor: pointer;
  }
  
  .order-button:hover {
    background-color: var(--color-black);
    color: var(--color-white);
    transition: 0.3s;
  }

  /*Desktop */

@media (min-width: 1200px) {

  .orderForm textarea:first-of-type {
    margin-bottom: 20px;
    height: 34px;
    padding: 4px 0 8px;
  }

  .orderForm-wrapper {
    flex-direction: row;
    justify-content: flex-start;
  }

  .orderForm__column {
    flex-grow: 1; 
  }

  .orderForm__column:first-of-type {
    margin-right: 35px;
  }

  .order-button {
    height: 70px;
  }
}